import bridge from './bridge.js'
const dfn = '_tc_ntv_map';

var map = {
   
};

let config = {  // n:实际调用native方法名 v：支持版本 ep：是否允许空参数
    location:{
        n:'app_locate',v:710
    },
    // checkIm:{
    //     n:'check_im_summary', v:810, cbcache:bridge.cacheName(dfn,'check_im_summary')
    // },
    navigationMap:{
        n:'open_navigation_map', v:730
    },
    city:{
        n:'select_city', v:730 , uncode:760
    },
    mapMark:{
        n:'show_multi_lonlats', v:740 
    },
    hotelMap:{
        n:'open_hotel_map', v:760 
    },
    flightCity:{
        n:'select_flight_city', v:804 
    },
    discoveryCity:{
        n:'select_discovery_city', v:804 
    },
    disportCity:{
        n:'select_disport_city', v:812 
    },
    trainCity:{
        n:'select_train_city', v:816 , uncode:816
    },
}
bridge.registerFn(map,config,dfn)

export default map;


